
@charset "UTF-8";
.storecenter {
  /* 领取标牌弹窗 */
  /* 去认证弹窗 */
}
.storecenter .main-info {
    justify-content: center;
    padding: .3rem .2rem .1rem;
    height: 3.12rem;
    border-bottom: 1px solid #eee;
}
.storecenter .main-info .flex-item {
      height: 2rem;
}
.storecenter .main-info .avatar {
      position: relative;
      width: 30%;
}
.storecenter .main-info .avatar a {
        overflow: hidden;
        display: block;
        margin: 0 auto;
        width: 1.88rem;
        height: 1.88rem;
        line-height: 1.88rem;
        border: 2px solid #fff;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        border-radius: 50%;
}
.storecenter .main-info .avatar a img {
          width: 100%;
          vertical-align: middle;
}
.storecenter .main-info .brief {
      width: 40%;
      text-align: center;
}
.storecenter .main-info .brief .store-name {
        height: 40%;
        padding-bottom: 0.15rem;
        font-size: 0.453rem;
        border-bottom: 1px solid #A5A5A8;
        line-height: 2;
        width: 100%;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
}
.storecenter .main-info .brief .account {
        justify-content: space-around;
        height: 58%;
}
.storecenter .main-info .brief .account .num {
          font-size: 0.459rem;
          line-height: 1.2;
          text-align: center;
}
.storecenter .main-info .brief .account .unit {
          color: #8A8A8C;
          font-size: 0.32rem;
}
.storecenter .main-info .edit {
      padding-top: 1.2rem;
      text-align: center;
      width: 30%;
}
.storecenter .main-info .edit a {
        display: block;
        margin: 0 auto;
        -webkit-border-radius: 4px;
        -moz-border-radius: 4px;
        -ms-border-radius: 4px;
        border-radius: 4px;
        width: 1.92rem;
        height: 0.813rem;
        line-height: 0.813rem;
        text-align: center;
        font-size: 0.37rem;
        color: #fff;
}
.storecenter .menulist .menu {
    position: relative;
    padding-left: .2rem;
    padding-right: .2rem;
    height: 1.2733rem;
    background: #fff;
}
.storecenter .menulist .menu .item {
      height: 99%;
      line-height: 1.3rem;
      text-indent: .8rem;
      font-size: 0.426rem;
}
.storecenter .menulist .menu .item a {
        display: block;
        width: 100%;
        height: 100%;
}
.storecenter .menulist .menu:not(.sign):after {
      content: '>';
      position: absolute;
      right: .4rem;
      top: 50%;
      transform: translateY(-50%);
      color: #8A8A8C;
      font-size: 0.46rem;
}
.storecenter .menulist .mar-bt {
    margin-bottom: 0.533rem;
}
.storecenter .menulist .bor-bt .item {
    border-bottom: 1px solid rgba(204, 204, 204, 0.4);
}
.storecenter .mint-popup .close {
    position: absolute;
    right: .3rem;
    top: .2rem;
    width: .6933rem;
    height: .6933rem;
    line-height: .7rem;
    text-align: center;
    font-size: .48rem;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    border-radius: 50%;
    border: 1px solid #eee;
}
.storecenter .getlabelpop {
    width: 100%;
}
.storecenter .getlabelpop .pop-wrap {
      position: relative;
      padding: 0 .2rem;
}
.storecenter .getlabelpop .pop-wrap .pop-item {
        overflow: hidden;
        line-height: 1.3rem;
}
.storecenter .getlabelpop .pop-wrap .pop-item .left {
          color: #707070;
          font-size: .453rem;
}
.storecenter .getlabelpop .pop-wrap .pop-item .right {
          float: right;
}
.storecenter .getlabelpop .pop-wrap .pop-item p {
          font-size: .48rem;
          text-align: right;
}
.storecenter .getlabelpop .pop-wrap .pop-item .labelpic {
          overflow: hidden;
          display: block;
          margin: -.3rem auto .02rem;
          width: 1.96rem;
          max-width: 1.96rem;
          height: 2.64rem;
          -webkit-box-shadow: 0 0 0 4px #fff, 0 0 6px 5px rgba(0, 0, 0, 0.2), inset 0 0 5px rgba(0, 0, 0, 0.4);
          -moz-box-shadow: 0 0 0 4px #fff, 0 0 6px 5px rgba(0, 0, 0, 0.2), inset 0 0 5px rgba(0, 0, 0, 0.4);
          -ms-box-shadow: 0 0 0 4px #fff, 0 0 6px 5px rgba(0, 0, 0, 0.2), inset 0 0 5px rgba(0, 0, 0, 0.4);
          box-shadow: 0 0 0 4px #fff, 0 0 6px 5px rgba(0, 0, 0, 0.2), inset 0 0 5px rgba(0, 0, 0, 0.4);
}
.storecenter .getlabelpop .pop-wrap .pop-item .labelpic img {
            width: 100%;
            height: 100%;
}
.storecenter .getlabelpop .pop-wrap .pop-item b {
          display: block;
          text-align: center;
          color: #AAAAAD;
          font-size: .34667rem;
}
.storecenter .getlabelpop .pop-wrap .pop-item:not(:last-of-type) {
        border-bottom: 1px solid #eee;
        border-bottom: 0.5px solid #eee;
}
.storecenter .getlabelpop .pop-wrap button {
        display: block;
        margin: .5rem auto .4rem;
        width: 9.33rem;
        height: 1.173rem;
        line-height: 1.17rem;
        color: #fff;
        font-size: .613rem;
        -webkit-border-radius: 4px;
        -moz-border-radius: 4px;
        -ms-border-radius: 4px;
        border-radius: 4px;
}
.storecenter .yetcertifiedpop img, .storecenter .completeinfo img {
    width: 1.76rem;
    height: 1.76rem;
}
.storecenter .yetcertifiedpop button a, .storecenter .completeinfo button a {
    display: block;
}
.storecenter .success .modal-wrap {
    padding-top: 3rem !important;
}
.storecenter .success .tip1 {
    margin-top: .6rem;
    margin-bottom: .3rem;
}
.storecenter .success .tip3 {
    margin-bottom: 1.2rem;
}
.storecenter .success button {
    display: none !important;
}
.storecenter .fail button {
    margin-top: 0.6rem !important;
}



