
.plus {
  color: #ea5504;
}
.minus {
  color: #04bd08;
}
.list {
  margin-bottom: 1.3rem;
  background: #fff;
}
.list li {
    margin: 0 .3rem;
    height: 1.8667rem;
}
.list li .item {
      overflow: hidden;
      height: 50%;
      line-height: 3;
}
.list li .item .name {
        float: left;
        font-size: .453rem;
}
.list li .item .money {
        float: right;
        font-size: .48rem;
}
.list li .exchange-time {
      line-height: 2;
      height: 50%;
      font-size: .34667rem;
      color: #818184;
}
.list li:not(:last-child) {
    border-bottom: 1px solid #ccc;
}



