
.myqresult {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.myqresult .info {
    display: flex;
    margin: 6.3rem auto 0;
    padding: 0 .3rem;
    width: 7.8667rem;
    height: 5.33rem;
}
.myqresult .info .left {
      margin-right: .3rem;
      width: 2rem;
}
.myqresult .info .left .avatar {
        position: relative;
        width: 100%;
}
.myqresult .info .left .avatar a {
          overflow: hidden;
          display: block;
          margin: 0 auto;
          width: 1.44rem;
          height: 1.44rem;
          line-height: 1.44rem;
          -webkit-border-radius: 50%;
          -moz-border-radius: 50%;
          -ms-border-radius: 50%;
          border-radius: 50%;
          border: 1px solid #ccc;
}
.myqresult .info .left .avatar a img {
            width: 100%;
            vertical-align: middle;
}
.myqresult .info .left .certificate {
        margin: .4rem auto;
        width: 1.76rem;
        height: 1.2rem;
        border: 1px solid #ccc;
        text-align: center;
}
.myqresult .info .left .certificate img {
          max-width: 100%;
          max-height: 100%;
}
.myqresult .info .right {
      flex: 1;
      color: #E4341D;
}
.myqresult .info .right .name {
        font-weight: 500;
}
.myqresult .info .right .item {
        overflow: hidden;
        margin-bottom: .18rem;
        font-size: .293rem;
}
.myqresult .info .right .item .name {
          float: left;
          width: 1.65rem;
          /*text-align-last: justify;*/
          text-align: justify;
}
.myqresult .info .right .item .content {
          margin-top: .05rem;
          float: right;
          width: calc(100% - 1.7rem);
          text-align: left;
          line-height: 1.2;
}
.myqresult .info .right .item .content span {
            display: inline-block;
}
.myqresult .info .right .certificate-number .content {
        line-height: 1.8;
}
.myqresult .scan {
    display: block;
    margin: 1rem auto .4rem;
    width: 4.54667rem;
    height: 1.1733rem;
    font-size: .613rem;
    color: #fff;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    border-radius: 4px;
}
.myqresult .tip {
    text-align: center;
    font-size: .32rem;
}



