
#performance {
  -webkit-user-select: none;
}
.date-input-wrap {
  border-bottom: 1px solid #ccc;
}
.overview-wrap {
  background: #fff;
  padding-left: .3rem;
  padding-right: .3rem;
}
.overview-wrap .overall-performance {
    justify-content: space-around;
    height: 1.733rem;
    border-bottom: 1px solid #ccc;
}
.overview-wrap .overall-performance .icon-rank a {
      display: block;
      width: 100%;
      height: 100%;
}
.overview-wrap .overall-performance .flex-item {
      width: 50%;
}
.overview-wrap .overall-performance .performance {
      display: flex;
      align-items: center;
      justify-content: flex-start;
}
.overview-wrap .overall-performance .performance .icon-performance {
        margin-left: .3rem;
}
.overview-wrap .overall-performance .performance .desc {
        margin-left: .8rem;
        color: #666;
        font-size: .37rem;
}
.overview-wrap .overall-performance .performance .desc em {
          font-size: .42rem;
          color: #000;
}
.overview-wrap .overview {
    justify-content: space-around;
    margin-bottom: .4rem;
    height: 1.733rem;
    font-size: .42667rem;
    text-align: center;
    line-height: 1.5;
    color: #666;
}
.overview-wrap .overview p {
      color: #000;
}
.search-form {
  background: #fff;
}
.result-wrap {
  margin-bottom: 1.4rem;
}
.result-wrap .flex {
    justify-content: space-between;
    margin-left: .3rem;
    margin-right: .3rem;
    height: 1.33rem;
}
.result-wrap .title {
    color: #666;
    font-size: .453rem;
}
.result-wrap .title .product-name {
      text-align: left;
}
.result-wrap .title .exchange-date {
      text-align: center;
}
.result-wrap .title .exchange-number {
      text-align: right;
}
.result-wrap .title .return-money {
      text-align: right;
}
.result-wrap .product-name {
    width: 30%;
}
.result-wrap .exchange-date {
    width: 35%;
}
.result-wrap .exchange-number {
    width: 15%;
}
.result-wrap .return-money {
    width: 20%;
}
.result-wrap .result {
    margin-bottom: 1rem;
    font-size: .373rem;
}
.result-wrap .result li:not(:last-of-type) {
      border-bottom: 1px solid #ccc;
}
.result-wrap .result .product-name {
      text-align: left;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
}
.result-wrap .result .exchange-date {
      color: #666;
      font-size: .32rem;
      text-align: center;
}
.result-wrap .result .exchange-number {
      text-align: right;
}
.result-wrap .result .return-money {
      color: #db0c07;
      text-align: right;
}



